:root {
  --main-background: #113e21;
  --accent-color: #b38b59;
  --dark-text-color: #131313;
  --light-text-color: #f1f1f1;
  --text-accent-color: #d6936d;
  --card-accent: #efd478;
  --hover-btn: #0a8817;
  --disabled: #ababab;
  --rights-reserved: #7a7a7a;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  overflow-y: scroll;
}

html, body {
  overflow-x: hidden;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4, ul, p {
  margin: 0;
  padding: 0;
}

ul, a {
  padding-left: 0;
  text-decoration: none;
  list-style: none;
}

body {
  letter-spacing: .04em;
  color: var(--dark-text-color);
  width: 100vw;
  margin: 0;
  font-family: Eb Garamond;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.75;
}

.no-scroll {
  overflow: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    padding-left: 45px;
    padding-right: 45px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .container {
    max-width: 768px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.header {
