  border-bottom: 2px solid rgba(96, 101, 99, .65);
}

.nav {
  height: 80px linear;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.drawer {
  position: relative;
  overflow: hidden;
}

.logo-text {
  color: #b38b59;
  font-family: Lobster;
  font-size: 36px;
}

.menu {
  justify-content: center;
  align-items: center;
  gap: 30px;
  font-size: 22px;
  font-weight: 600;
  line-height: 1.25;
  display: flex;
}

@media screen and (max-width: 767px) {
  .menu {
    display: none;
  }
}

.menu-link {
  color: var(--light-text-color);
  text-shadow: 4px 6px 6px #000;
  transition: all .3s;
}

.menu-link:hover, .menu-link:focus {
  color: var(--accent-color);
  text-shadow: 4px 6px 6px #000;
}

.burger-btn {
  background-color: var(--main-background);
  cursor: pointer;
  fill: #b38b59;
  border-width: 0;
  margin-left: auto;
  position: relative;
}

.burger-btn:hover {
  transform: scale(1.1);
}

@media screen and (min-width: 768px) {
  .burger-btn {
    display: none;
  }
}

.burger-menu {
  z-index: 20;
  -webkit-backdrop-filter: blur(40px);
  backdrop-filter: blur(40px);
  width: 75%;
  height: 100%;
  transition: all .3s linear;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  overflow: hidden;
}

@media screen and (min-width: 767px) {
  .burger-menu {
    display: none;
  }
}

.is-hidden {
  display: block;
}

.burger-head {
  justify-content: flex-end;
  align-items: center;
  margin: 15px 0;
  display: flex;
}

