  display: flex;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .hero {
    background: linear-gradient(145deg, var(--main-background) 50%, #f1f1f1 50%);
    background: var(--main-background);
  }
}

.box-decoration {
  max-width: 100%;
  width: 500px;
  background: rgba(255, 255, 255, .067);
  border: 2px solid rgba(255, 255, 255, .173);
  border-radius: .6em;
  flex-direction: column;
  margin: 0 20px;
  padding: 45px 25px;
  display: flex;
  box-shadow: -2px -2px 3px #3a6b4b, 2px 2px 3px rgba(194, 190, 105, .36);
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .box-decoration {
    margin: 0 10px;
