  .box-decoration {
    margin-top: 20px;
  }
}

.hero-section {
  justify-content: center;
  align-items: center;
  gap: 200px;
  display: flex;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .hero-section {
    gap: 50px;
  }
}

@media screen and (min-width: 320px) and (max-width: 768px) {
  .hero-section {
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
    gap: 30px;
    display: flex;
  }
}

.hero-title {
  text-transform: uppercase;
  color: var(--light-text-color);
  text-shadow: 4px 6px 6px #000;
  font-size: 30px;
  line-height: 1.27;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .hero-title {
    font-size: 30px;
  }
}

.products-link {
  color: var(--disabled);
  text-shadow: 1px 1px 2px var(--dark-text-color), 0 0 1em var(--dark-text-color);
  max-width: 300px;
  border-radius: 10px;
  margin: 30px 12px 0 0;
  padding: 12px 34px;
  font-size: 24px;
  box-shadow: inset -2px -2px 5px rgba(179, 138, 89, .59), inset 2px 2px 5px rgba(179, 138, 89, .61);
}

@media screen and (min-width: 320px) and (max-width: 1199px) {
  .products-link {
    margin-right: 8px;
    padding: 8px 12px;
  }
}

.my-name {
  -webkit-box-reflect: below -30px linear-gradient(transparent, rgba(0, 0, 0, .2));
  display: inline-block;
  position: relative;
}

.my-name span {
  display: inherit;
  color: var(--accent-color);
  animation: my-name 3s 4;
  animation-delay: calc(.1s * var(--i) );
  font-family: Lobster, sans-serif;
  font-size: 40px;
  position: relative;
}

@keyframes my-name {
  0%, 40%, 100% {
    transform: translateY(0);
  }

  20% {
    transform: translateY(-10px);
  }
}

.hero-img {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 30px;
  display: flex;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .hero-img {
    margin: 0 15px;
  }
}

.photo {
  height: 310px;
  width: 260px;
  box-shadow: -4px -4px 15px var(--main-background), 5px 5px 15px var(--main-background);
  border-radius: 50%;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .photo {
    height: 250px;
    width: 250px;
  }
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .photo {
    height: 270px;
    width: 230px;
    filter: drop-shadow(5px 5px 15px var(--light-text-color) );
  }
}

.button-cv {
  background: var(--main-background);
  height: 40px;
  width: 150px;
  border-radius: 60px;
  position: relative;
  top: 20px;
}

.button-cv:hover {
  transform: scale(1.1);
}

.link-cv {
  color: #f1f1f1;
}

.wer {
  color: #0ff;
}

.info {
  background-color: red;
}

